import React, { Component } from 'react';
import Navbar from "../Dash/navbar/Navbar";
import Sidebar from "../Dash/sidebar/Sidebar";
import PropTypes from 'prop-types';
import Schedule from '../../calender/Schedule/Schedule';
//import { Test } from './Dash.styles{';

class Dash extends Component { 
  constructor(props) {
    super(props);

    this.state = {
      hasError: false,
      close: false
    };
    this.handleToggle = this.handleToggle.bind(this);
  }
  handleToggle = () => {
    this.setState({
      close: !this.state.close
    });
  }

  componentWillMount = () => {
    console.log('Dash will mount');
  }

 

  render () {
    ;
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    return (
      <div >   

                 <div id="wrapper">
                     <Sidebar close={this.state.close}  handleToggle={this.handleToggle}  />
                    
                    <div className="d-flex flex-column" id="content-wrapper">
                         <div id="content">
                             <Navbar hop={this.handleToggle} />
                         

                         <Schedule />
                     </div>
                     </div>
      </div>
      </div>
    )
  }
}

Dash.propTypes = {
  // bla: PropTypes.string,
};

Dash.defaultProps = {
  // bla: 'test',
};

export default Dash;
