import React, { Component } from 'react';
import '../../../../../assets/css/untitled.css';

//import { Test } from './Sidebar.styles';

class Sidebar extends Component { 
  constructor(props) {
    super(props);

    this.state = {
      hasError: false,
      sideClass: "navbar navbar-dark align-items-start sidebar sidebar-dark accordion p-0 "
    };
  
    
   
  }

  
  
  

  componentWillMount = () => {
    console.log('Sidebar will mount');
  }

  render () {
    const sideStyle = "navbar navbar-dark align-items-start sidebar sidebar-dark accordion p-0 ";
    const tClass = this.props.close ? 'showSide' : 'hideSide'
    var menu = sideStyle + tClass;
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }
    return (
     
         // eslint-disable-next-line no-template-curly-in-string
         <nav className={menu} id="sidebar" style={{backgroundColor: "#0c3026"}} >
            <div className="container-fluid d-flex flex-column p-0">
                <a className="navbar-brand d-flex justify-content-center align-items-center sidebar-brand m-0" href="#" >
                    <div className="sidebar-brand-icon rotate-n-15">
                      
                        </div>
                    <div className="sidebar-brand-text mx-3" style={{display: 'none'}}>
                      <span style={{background: "transparent", color: "#f4ed47"}}>Y.S Enhance&nbsp;</span>
                      </div>
                </a>
                <hr className="sidebar-divider my-0" />
                <ul className="nav navbar-nav text-light" id="accordionSidebar">
                    <li className="nav-item"><a class="nav-link" href="login.html"><i class="far fa-user-circle"></i><span>Login</span></a></li>
                    <li className="nav-item"><a class="nav-link" href="register.html"><i class="fas fa-user-circle"></i><span>Register</span></a></li>
                    <li className="nav-item dropdown"><a className="dropdown-toggle nav-link fas fa-table" data-toggle="dropdown" aria-expanded="false" href="#">&nbsp;<span>Schedule</span></a>
                        <div className="dropdown-menu" style={{backgroundImage: "linear-gradient(45deg, #0f6848, #0f6848)"}}><button className="dropdown-item" href="#">First Item</button><button className="dropdown-item" href="#">Second Item</button><button className="dropdown-item" href="#">Third Item</button></div>
                    </li>
                </ul>
                <div className="text-center d-md-inline">
                  <button className="btn rounded-circle border-0" id="sidebarToggle"  type="button" onClick={ this.props.handleToggle }>ii</button>
                  </div>
            </div>
        </nav>
     
    );
  }
}


export default Sidebar;
